import { request } from 'utils';

const prefix = '/api/categories';

export const getConvertedPointsPractices = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/PracticeClassification',
    method: 'GET',
    params: { label, labelInEnglish, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const deleteConvertedPointsPractices = id =>
  request({
    prefix,
    url: `/PracticeClassification/${id}`,
    method: 'DELETE',
  });

export const createConvertedPointsPractices = ({
  label,
  labelInEnglish,
  fromGrade,
  toGrade,
  gradeIn10,
  gradeIn4,
}) =>
  request({
    prefix,
    url: '/PracticeClassification',
    data: { label, labelInEnglish, fromGrade, toGrade, gradeIn10, gradeIn4 },
    method: 'POST',
  });

export const updateConvertedPointsPractices = ({
  id,
  label,
  labelInEnglish,
  fromGrade,
  toGrade,
  gradeIn10,
  gradeIn4,
}) =>
  request({
    prefix,
    url: `/PracticeClassification/${id}/Grade`,
    data: { id, label, labelInEnglish, fromGrade, toGrade, gradeIn10, gradeIn4 },
    method: 'PUT',
  });

export const getConvertedPointsPracticesById = id =>
  request({
    prefix,
    url: `/PracticeClassification/${id}`,
    method: 'GET',
  });



