import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import {
  getConvertedPointsPracticesById,
  createConvertedPointsPractices,
  updateConvertedPointsPractices,
} from 'services/convertedPointsPractice';

@Form.create({ name: 'formConvertGraded' })
class FormConvertedTrainingPoints extends Component {
  constructor(props) {
    super(props);
    this.state = {
      gradeId: null,
      initialValue: {},
      disableField: false,
    };
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ gradeId: match.params.id });
      this.setState({ disableField: true });
      this.getGradedById(match.params.id);
    }
  };

  getGradedById = async id => {
    const res = await getConvertedPointsPracticesById(id);
    console.log(res);
    const initialValue = this.handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      label: '',
      labelInEnglish: '',
      fromGrade: '',
      toGrade: '',
      gradeIn10: '',
      gradeIn4: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { gradeId } = this.state;
    try {
      if (!gradeId) {
        await createConvertedPointsPractices(values);
      } else {
        await updateConvertedPointsPractices({ id: gradeId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  filterField = () => {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { gradeId } = this.state;
    if (gradeId) {
      return (
        <div>
          <Form.Item {...formItemLayout} label="Điểm cộng thang 10" hasFeedback>
            {getFieldDecorator('gradeIn10', {
              initialValue: initialValue.gradeIn10,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng nhập thông tin!',
                },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="Điểm cộng thang 4" hasFeedback>
            {getFieldDecorator('gradeIn4', {
              initialValue: initialValue.gradeIn4,
              rules: [
                {
                  required: true,
                  message: 'Vui lòng nhập thông tin!',
                },
              ],
            })(<Input />)}
          </Form.Item>
        </div>
      );
    }
    return null;
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { gradeId } = this.state;
    return (
      <Card title={gradeId ? 'Cập nhật quy đổi rèn luyện' : 'Thêm quy đổi điểm rèn luyện'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="Xếp loại rèn luyện" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={this.state.disableField} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={this.state.disableField} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={this.state.disableField} />)}
            </Form.Item>
            {this.filterField()}
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormConvertedTrainingPoints {...props} />
  </Page>
));



